anded=true]):hover \r\n    box-shadow: rgba(2, 8, 20, 0.1) 0px 0.35em 1.175em, rgba(2, 8, 20, 0.08) 0px 0.175em 0.5em\r\n    transform: translateY(-3px) scale(1.01)\r\n    /*border-radius: 30px*/\r\n    /*filter: brightness(105%)*/\r\n\r\n.animCard[data-expanded=true]\r\n    background: #333333\r\n    transform: translateY(-3px)\r\n    transition: none\r\n\r\n.entering\r\n    animation: cardboardEntrance 300ms ease-out\r\n    animation-fill-mode: backwards    \r\n    \r\n.list\r\n    display: flex\r\n    flex-direction: column\r\n    justify-content: space-around\r\n    padding: 0% 10%\r\n    //overflow: hidden\r\n\r\n    @media screen and (max-width: 600px)  \r\n        padding: 0% 1.5%\r\n        font-size: 14px\r\n\r\n@media screen and (max-width: 600px) \r\n    .animCard[data-expanded=false]:hover \r\n        transform: translateY(-3px) scale(1.01)\r\n \r\n@keyframes cardboardEntrance \r\n    from \r\n        opacity: 0\r\n        transform: scaleY(0.3)\r\n  \r\n    to \r\n        opacity: 1\r\n        transform: scaleY(1)\r\n\r\n@keyframes cardEntrance \r\n    from \r\n        opacity: 0\r\n        transform: scale(0.3)\r\n        filter: hue-rotate(180deg)\r\n  \r\n    to \r\n        opacity: 1\r\n        transform: scale(1)\r\n        filter: hue-rotate(0deg)\r\n\r\n.fabulous\r\n    font-weight: bolder\r\n    align-self: center\r\n    line-height: 1.3em\r\n    background-image: linear-gradient(135deg,#ff00fe, #00f3ff)\r\n    background-clip: text\r\n    -webkit-text-fill-color: transparent\r\n    color: #00000000\r\n\r\n.scroll\r\n    max-height: 100%\r\n    overflow-y: scroll\r\n    scrollbar-width: thin\r\n    padding: 0 3px 0 0\r\n\r\n.centerAbsolute\r\n    top: 0\r\n    left: 0\r\n    bottom: 0\r\n    right: 0\r\n    margin: auto\r\n    position: absolute \r\n    width: max-content\r\n    height: max-content\r\n    text-align: center\r\n\r\n.wrong\r\n    animation: wrong 600ms, redflash 800ms\r\n\r\n@keyframes wrong\r\n    0%\r\n        transform: translate(0px, 0px)\r\n    12.5%\r\n        transform: translate(20px, 0px)\r\n    37.5%\r\n        transform: translate(-20px, 0px)\r\n    62.5%\r\n        transform: translate(20px, 0px)\r\n    87.5%\r\n        transform: translate(-20px, 0px)\r\n    100%\r\n        transform: translate(0px, 0px)\r\n\r\n@keyframes redflash\r\n    0%\r\n        background-color: #353535\r\n    40%\r\n        background-color: #740000 \r\n    60%\r\n        background-color: #740000 \r\n    100%\r\n        background-color: #353535",".card, .postForm, .animCard, .post, .userEntry {\n  display: flex;\n  flex-direction: row;\n  justify-content: center;\n  align-items: flex-start;\n  background: #353535;\n  color: #fff;\n  box-shadow: rgba(3, 8, 20, 0.1) 0px 0.15rem 0.5rem, rgba(2, 8, 20, 0.1) 0px 0.075rem 0.175rem;\n  height: 100%;\n  width: 100%;\n  border-radius: 4px;\n  transition: all 250ms ease-in-out;\n  overflow: hidden;\n  background-size: cover;\n  background-position: center;\n  background-repeat: no-repeat;\n}\n\n.fallbackImg {\n  overflow: hidden;\n}\n.fallbackImg img {\n  height: 100%;\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.animCard, .post, .userEntry {\n  animation: cardEntrance 700ms ease-out;\n  animation-fill-mode: backwards;\n  will-change: transform;\n}\n\n.animCard:not([data-expanded=true]):hover, .post:not([data-expanded=true]):hover, .userEntry:not([data-expanded=true]):hover {\n  box-shadow: rgba(2, 8, 20, 0.1) 0px 0.35em 1.175em, rgba(2, 8, 20, 0.08) 0px 0.175em 0.5em;\n  transform: translateY(-3px) scale(1.01);\n  /*border-radius: 30px*/\n  /*filter: brightness(105%)*/\n}\n\n.animCard[data-expanded=true], [data-expanded=true].post, [data-expanded=true].userEntry {\n  background: #333333;\n  transform: translateY(-3px);\n  transition: none;\n}\n\n.entering {\n  animation: cardboardEntrance 300ms ease-out;\n  animation-fill-mode: backwards;\n}\n\n.list, .userList {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-ar